    >  
     <el-row>
                    <el-col :span="12">
           <el-form-item v-if="flag=='shangjia'"  label='' prop="shangjiaName">
               <el-input v-model="ruleForm.shangjiaName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='shangjia'"  label='' prop="shangjiaPhone">
               <el-input v-model="ruleForm.shangjiaPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='shangjia'"  label='' prop="shangjiaEmail">
               <el-input v-model="ruleForm.shangjiaEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='shangjia'" label='' prop="shangjiaPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
