                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='shangjia'"  label='' prop="shangjiaXingjiTypes">
                 <el-select v-model="ruleForm.shangjiaXingjiTypes" disabled placeholder=''>
                     <el-option
                             v-for="(item,index) in shangjiaXingjiTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='shangjia'"  label='' prop="newMoney">
                 <el-input v-model="ruleForm.newMoney"  placeholder='' disabled style="width: 100px"></el-input>
                 <!--<a id="btn-recharge" @click="chongzhi" href="javascript:void(0)"></a>-->
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item v-if="flag=='shangjia'"  label="" prop="shangjiaContent">
                 <editor
                         style="min-width: 200px; max-width: 600px;"
                         v-model="ruleForm.shangjiaContent"
                         class="editor"
                         action="file/upload">
                 </editor>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuName">
               <el-input v-model="ruleForm.yonghuName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuPhone">
               <el-input v-model="ruleForm.yonghuPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuIdNumber">
               <el-input v-model="ruleForm.yonghuIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'" label='' prop="yonghuPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.yonghuPhoto?ruleForm.yonghuPhoto:''"
                         @change="yonghuPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuEmail">
               <el-input v-model="ruleForm.yonghuEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'"  label='' prop="newMoney">
                 <el-input v-model="ruleForm.newMoney"  placeholder='' disabled style="width: 100px"></el-input>
                 <!--<a id="btn-recharge" @click="chongzhi" href="javascript:void(0)"></a>-->
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuYesnoTypes">
                 <el-select v-model="ruleForm.yonghuYesnoTypes"  placeholder=''>
                     <el-option
                             v-for="(item,index) in yonghuYesnoTypesOptions"
