                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-form-item v-if="flag=='users'" label="" prop="username">
             <el-input v-model="ruleForm.username"
                       placeholder=""></el-input>
         </el-form-item>
         <el-col :span="12">
             <el-form-item v-if="flag!='users' && flag!='shangjia'"  label="" prop="sexTypes">
                 <el-select v-model="ruleForm.sexTypes" placeholder="">
                     <el-option
                             v-for="(item,index) in sexTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item>
                 <el-button type="primary" @click="onUpdateHandler"> </el-button>
             </el-form-item>
         </el-col>
     </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
        ruleForm: {},
        flag: '',
        usersFlag: false,
        // sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
        // 
        yonghuYesnoTypesOptions : [],
//  
    //  
        // 
        shangjiaXingjiTypesOptions : [],
    };
  },
  mounted() {
    //
    var table = this.$storage.get("sessionTable");
    this.sessionTable = this.$storage.get("sessionTable");
    this.role = this.$storage.get("role");
    if (this.role != ""){
    }

    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
//  
//  
      } else {
        this.$message.error(data.msg);
      }
    });

//   
//   

      this.$http({
          url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=sex_types`,
          method: "get"
      }).then(({ data }) => {
          if (data && data.code === 0) {
              this.sexTypesOptions = data.data.list;
          } else {
              this.$message.error(data.msg);
          }
      });
   this.$http({
       url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=shangjia_xingji_types`,
       method: "get"
   }).then(({ data }) => {
       if (data && data.code === 0) {
          this.shangjiaXingjiTypesOptions = data.data.list;
      } else {
          this.$message.error(data.msg);
      }
    });
   this.$http({
       url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=yonghu_yesno_types`,
       method: "get"
   }).then(({ data }) => {
       if (data && data.code === 0) {
          this.yonghuYesnoTypesOptions = data.data.list;
      } else {
          this.$message.error(data.msg);
      }
    });
  },
  methods: {
    chongzhi() {
      this.$router.replace({ path: "/pay" });
    },
    shangjiaPhotoUploadChange(fileUrls) {
        this.ruleForm.shangjiaPhoto = fileUrls;
    },
    yonghuPhotoUploadChange(fileUrls) {
        this.ruleForm.yonghuPhoto = fileUrls;
    },


    onUpdateHandler() {
                         if((!this.ruleForm.shangjiaName)&& 'shangjia'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.shangjiaPhone)&& 'shangjia'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'shangjia' ==this.flag && this.ruleForm.shangjiaEmail&&(!isEmail(this.ruleForm.shangjiaEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.shangjiaPhoto)&& 'shangjia'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.shangjiaXingjiTypes)&& 'shangjia'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.shangjiaContent)&& 'shangjia'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuName)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yonghu' ==this.flag && this.ruleForm.yonghuPhone&&(!isMobile(this.ruleForm.yonghuPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.yonghuIdNumber)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuPhoto)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yonghu' ==this.flag && this.ruleForm.yonghuEmail&&(!isEmail(this.ruleForm.yonghuEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.yonghuYesnoTypes)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

        if((!this.ruleForm.sexTypes) && this.flag!='users' && this.flag!='shangjia'){
            this.$message.error('');
            return
        }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
        this.$message.error(``);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
